.features {
	&__list {
		width: 881px;
		margin: 0 auto; }
	&__item {
		padding: 0 50px;
		text-align: center;
		display: inline-block;
		width: 24%;
		color: #000000;
		font-family: Akrobat;
		font-size: 16px;
		font-weight: 300;
		text-transform: uppercase;
		&:before {
			display: inline-block;
			content: '';
			@include s(gift);
			margin-bottom: 35px; }
		&_logistick:before {
			margin-left: -25px;
			@include s(logistick); }
		&_design:before {
			@include s(design); }
		&_nature:before {
			@include s(nature); } } }
